import s from "./DeleteLocation.module.css";
import {useDispatch} from "react-redux";
import {deleteLocation} from "../../../store/reducers/locationsReducer";

const DeleteLocation =({location, id, onClose})=> {
    const dispatch = useDispatch();
    const deleteHandler = () => {
        dispatch(deleteLocation(id));
        onClose();
    }

    return (
        <>
            <div className={s.title}>
                Are you sure want to delete {location} Location?
            </div>
            <div className={s.mainInfo}>
                <svg width="17" height="16" viewBox="0 0 17 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="evenodd" clipRule="evenodd" d="M8.32128 0.777726C7.54178 0.899241 6.8359 1.346 6.39859 1.99463C6.25281 2.21082 1.14797 11.0519 1.01854 11.3124C0.57669 12.2013 0.65963 13.3045 1.23182 14.1489C1.61738 14.7179 2.20648 15.1314 2.88897 15.3122L3.15511 15.3827H8.697C14.6392 15.3827 14.396 15.3889 14.857 15.2246C15.278 15.0746 15.5711 14.8881 15.8945 14.5648C16.1517 14.3076 16.3137 14.0763 16.4794 13.7297C16.6792 13.3116 16.7368 13.0439 16.7368 12.5335C16.7368 12.0241 16.6792 11.7553 16.4812 11.341C16.365 11.098 11.4567 2.57098 11.1588 2.09472C10.8599 1.61671 10.3017 1.1346 9.85062 0.964835C9.37246 0.784865 8.7579 0.709658 8.32128 0.777726ZM8.99445 2.06185C9.36989 2.12171 9.74467 2.34251 9.95958 2.63044C10.0986 2.81674 15.1851 11.6148 15.3228 11.9073C15.5369 12.3618 15.4875 12.9369 15.1952 13.3924C14.9574 13.7629 14.5368 14.0245 14.0823 14.0843C13.913 14.1067 12.1289 14.1144 8.57176 14.1081L3.31166 14.099L3.10063 14.0274C2.23851 13.7348 1.79096 12.7546 2.14946 11.944C2.21249 11.8015 7.23069 3.08901 7.45368 2.73495C7.70234 2.3401 8.21426 2.05462 8.71266 2.03279C8.75571 2.03091 8.88251 2.044 8.99445 2.06185ZM8.54462 5.26995C8.2808 5.33695 8.07991 5.52487 7.9594 5.81744C7.90028 5.96093 7.90364 6.16097 7.98867 7.55515C8.02756 8.19231 8.07731 9.01091 8.09926 9.37423C8.14729 10.1691 8.17694 10.2532 8.45995 10.3965C8.6527 10.4942 8.7878 10.5044 8.95631 10.4341C9.2268 10.3213 9.37164 10.0642 9.36879 9.70202C9.36804 9.60615 9.41344 8.73595 9.4697 7.76828C9.58239 5.8294 9.58295 5.84637 9.39973 5.58004C9.22286 5.32299 8.85753 5.19051 8.54462 5.26995ZM8.52461 11.0736C8.14673 11.1976 7.90761 11.5389 7.93585 11.914C7.96979 12.3642 8.29651 12.6721 8.7419 12.6737C9.05989 12.6748 9.32292 12.5119 9.47158 12.222C9.55149 12.0662 9.56013 11.7141 9.48818 11.5449C9.42118 11.3873 9.25739 11.2104 9.10792 11.1341C8.96999 11.0638 8.65436 11.031 8.52461 11.0736Z" fill="#F24445"/>
                </svg>
                <p>Deleting a location might impact the users' configuration and leave information (e.g. the initial brought forward days).</p>
            </div>
            <div className={s.btnWrapper}>
                <div onClick={() => deleteHandler()} className={s.deleteBtn}>Yes, Delete</div>
            </div>
        </>
    )
}

export default DeleteLocation